<template>
  <div class="animated fadeIn">
    <!-- Selected: {{ selectedName }} -->
    <div v-on:click="resetTemplate()">
      Clear
    </div>

    <b-card :header="selectedName">
      <!-- <b-table :bordered="true" :fixed="true" responsive="sm" :items="dataObj" :fields="selectedTemplate.columns"> -->
        <!-- <template slot="status" slot-scope="data"> -->
          <!-- <b-badge :variant="getBadge(data.item.status)">{{data.item.status}}</b-badge> -->
        <!-- </template> -->
      <table >
        <thead>
          <tr>
            <th v-for="(eachColumn, colIndex) in selectedTemplate.columns" v-bind:key="colIndex">
              {{ eachColumn }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(eachRow, rowIndex) in dataObj" v-bind:key="rowIndex">
            <td v-for="(eachColumn, colIndex) in selectedTemplate.columns" v-bind:key="colIndex">
              <!-- <b-form-input type="text" placeholder="" v-model="newRowObj[eachColumn]" v-on:change="newRowChanged()"></b-form-input> -->
              <b-form-input type="text" placeholder="" v-model="newRowObj[eachColumn]"></b-form-input>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- </b-table> -->
      <span v-on:click="addNewRow">Add New Row</span>
      <b-button type="submit" variant="primary" v-on:click="clkSaveData">Save & Publish</b-button>
    </b-card>

  </div>
</template>

<script>
let templatesRef = firebase.database().ref('templates');
import store from '../store';

export default {
  name: 'postTemplate',
  // firebase: {
  //   templates: templatesRef
  // },
  data() {
    return {
      selectedTemplate: {},
      selectedKey: 0,
      dataObj: [],
      newRowObj: {}
      // isNewRowInValid: true
    }
  },
  computed: {
    selectedName: () => store.state.selectedTemplateName
  },
  created: function() {
    templatesRef.on('value', snapshot => {
      for ( let key of Object.keys(snapshot.val()) ) {
        if( snapshot.val()[key].name === this.selectedName ) {
          this.selectedTemplate = snapshot.val()[key];
          this.selectedKey = key;
          for (let n=0; n < this.selectedTemplate.columns.length; n++) {
            this.newRowObj[ this.selectedTemplate.columns[n] ] = '';
          }
          this.dataObj.push(this.newRowObj);
          break;
        } // IF
      } // FOR
    });
  },
  methods: {
    resetTemplate() {
      store.commit('clearSelected');
    },
    addNewRow() {
// console.log('BEFORE -> ',this.dataObj);
      // this.dataObj.push(this.newRowObj);
// console.log('AFTER -> ',this.dataObj);
      this.newRowObj = {};
      for (let n=0; n < this.selectedTemplate.columns.length; n++) {
        this.newRowObj[ this.selectedTemplate.columns[n] ] = '';
      }
      this.dataObj.push(this.newRowObj);
    },
//     newRowChanged() {
//       this.isNewRowInValid = true;
// console.log('CHANGING...');

//       for (let d=0; d < this.selectedTemplate.columns.length; d++) {
// console.log('VALUE[',d,'] -> ', this.newRowObj[ this.selectedTemplate.columns[d] ]);

//         if ( this.newRowObj[ this.selectedTemplate.columns[d] ] !== '' ) {
// console.log('ENABLE...');
//           this.isNewRowInValid = false;
//         }
//       }
//     },
//     isNewRowInValid() {
//       for (let d=0; d < this.selectedTemplate.columns.length; d++) {
//         if ( this.newRowObj[d] === '' ) {
// console.log('RETURNING');

//           return false;
//         }
//       }
//       return true;
//     },
    clkSaveData() {
console.log(this.dataObj);
    }
  }
}
</script>

<style>
 /* ADD DISABLE-SELCT */
</style>



