<template>
  <div class="animated fadeIn">
    <manage-templates card-title="Choose a Template" v-if="templateSelected === ''" />
    <post-template v-if="templateSelected !== ''"></post-template>
  </div>
  
</template>

<script>

import ManageTemplates from '@/views/ManageTemplates'
import PostTemplate from '@/views/PostTemplate'

import store from '../store';

export default {
  name: 'postNew',
  components: { ManageTemplates, PostTemplate },
  // data() {
  //   return {
  //     // templateSelected: []
  //   }
  // },
  // methods: {
  // },
  // created: function() {
  // },
  computed: {
    templateSelected: () => store.state.selectedTemplateName
  }
}
</script>

